
*, *::before, *::after{
    box-sizing: border-box
}
html, body{
    font: 16px/1.2 BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    padding: 10px
}
.wrapper{
    width: 75%;
    margin: 0 auto;
}
form{
    margin-bottom: 20px
}
input[type="text"]{
    width: 100%;
    padding: 10px;
    border: 1px solid #777
}
ul, li{
    margin: 0;
    padding: 0
}
p.none{
    color: #888;
    font-size: small
}
  